@import "mixins";

body {
	font-family: 'Montserrat', sans-serif;
	background: #f9f9f9;
	min-width: 320px; }
header {
	margin: auto;
	// max-width: $max-w
	background: $main-blue; }

.header_first_block {
	padding: 9px 0;
	font-size: 14px;
	background: #004ebd;
	.container {
		@include flex;
		justify-content: space-between; }
	.header_location {
		img {
			margin-top: -3px; }
		a {
			color: #fff; } }
	.header_social {
		a, i, span {
			color: #fff; }
		div {
			display: inline-block; }
		.header_social_icons {
			margin-right: 10px;
			img {
				margin-top: -3px; }
			a {
				display: inline-block;
				margin-right: 10px; } }
		.header_social_mail {
			img {
				margin-top: -3px;
				margin-right: 8px; } } } }

.container {
	width: 1170px; }

.menu_button {
	display: none;
	color: #fff;
	span {
		vertical-align: middle; } }


.nav_container {
	@include flex;
	justify-content: space-between;
	align-items: center;
	padding-top: 7px;
	padding-bottom: 7px;
	position: relative; }
.navbar {
	padding-left: 0;
	padding-right: 0;
	// justify-content: space-between !important
	.navbar-collapse {
		flex-grow: 0; }
	.nav-link {
		color: #fff !important; } }
.navbar_contacts {
	color: #fff;
	&_phone, &_promo {
		display: inline-block; }
	&_phone {
		text-align: right;
		margin-right: 20px; }
	&_promo {
		padding: 5px 30px 5px 15px;
		background: url(../img/button-bg.png) no-repeat 50% 50%;
		background-size: 150% 200%;
		img {
			vertical-align: unset;
			margin-right: 15px; } }
	.promo_caption {
		// vertical-align: middle
		display: inline-block;
		div {
			font-size: 16px;
			line-height: 21px; } } }
.navbar-toggler-icon {
	background: url('../img/menu_button.svg') no-repeat 50% 50%; }


.main_content {
	padding-top: 30px; }

.main_content {

	.main_page_left_column {
		// max-width: 220px
		width: 24%;
		max-width: 24%; }

	.main_page_right_column {
		// max-width: 862px
		width: 76%;
		max-width: 76%; }

	.side_bar_menu {
		margin-bottom: 25px;
		.side_bar_menu_capt {
			background: $main-blue;
			color: #fff;
			padding: 23px 22px;
			position: relative;
			cursor: pointer;
			h6 {
				margin: 0; }
			img {
				position: absolute;
				right: 15px;
				top: 50%;
				@include transform(translate(0%, -50%)); } }
		.side_bar_menu_list {
			list-style-type: none;
			padding: 0;
			margin: 0;
			border: 1px solid #efefef;
			// .side_bar_menu_list_item:after
			// 	content: url('../img/list_item_arrow.png')
			// 	position: absolute
			// 	right: 15px
			.side_bar_menu_list_item:last-child {
				border: none; }
			.side_bar_menu_list_item {
				position: relative;
				background: #fff;
				border-bottom: 1px solid #efefef;
				a:after {
					content: url('../img/list_item_arrow.png');
					position: absolute;
					right: 15px; }
				a:hover {
					text-decoration: none;
					font-weight: bold; }
				a {
					@include transition-ease(0.3s);
					display: block;
					padding: 15px;
					div:nth-child(1) {
						display: inline-block;
						width: 25px;
						text-align: center;
						margin-right: 10px; }

					// @include flex
					// justify-content: space-around
					// img
					/// width: 20%
					// span
 } } } }					/// width: 75%

	.sidebar_offer {
		border: 1px solid #efefef;
		overflow: hidden;
		margin-bottom: 30px;
		.sidebar_offer_h {
			background: #fff;
			padding: 25px 30px 20px;
			h4 {
				font-size: 25px;
				font-weight: bold; } }
		.sidebar_offer_text {
			padding: 22px 30px 20px;
			font-size: 25px;
			font-weight: bold;
			background: #ededed; }
		.sidebar_offer_img {
			background: #fff;
			position: relative;
			height: 195px;
			img {
				position: absolute;
				top: -50px;
				right: 0px; } } }

	.main_page_right_column_form {
		position: relative;
		.main_page_right_column_btn:focus,
		.main_page_right_column_search:focus {
			outline: none !important;
			box-shadow: none !important; }
		.main_page_right_column_search {
			padding: 23px 40px 23px 25px;
			margin-right: 0 !important;
			width: 100%;
			border-radius: 0;
			border: 1px solid #efefef; }
		.main_page_right_column_btn {
			padding: 0;
			background: none;
			position: absolute;
			right: 15px;
			margin: 0 !important; } }

	.main_page_slider_hit {
		margin-top: 30px;
		margin-right: 0;
		justify-content: space-between;
		.slider_block {
			// max-width: 600px
			max-width: 72%;
			width: 72%;
			min-width: 448px;
			padding-right: 20px;
			.slick_slide {
				img {
					// width: 100%
 } } }					// height: 100%
		.slick-prev, .slick-next {
			display: none !important; }
		.slick-dots {
			bottom:  30px;
			left: 30px;
			width: auto; }
		.slick-dotted.slick-slider {
			margin-bottom: 0; }
		.slick-dots {
			li {
				button:before {
					font-size: 16px;
					opacity: 0.2;
					color: #fff; } }
			li.slick-active {
				button:before {
					// color: #fff
					opacity: 1; } } }

		.hit_block {
			padding: 30px 20px;
			// max-width: 240px
			max-width: 28%;
			width: 28%;
			min-width: 220px;
			background: #fff;
			border: 1px solid #efefef;
			text-align: center; }
		.hit_block_article_img {
			position: relative;
			width: 100%;
			height: 200px;
			border: 1px solid #efefef;
			overflow: hidden;
			img {
				max-width: 100%;
				position: absolute;
				top: 50%;
				left: 50%;
				@include transform(translate(-50%, -50%));
				z-index: 1; }
			.hit_block_article_img_caption:before {
				content: '';
				width: 0;
				height: 0;
				border-left: 17px solid transparent;
				// border-right: 20px solid transparent
				border-top: 40px solid #fcd43e;
				position: absolute;
				left: -16px;
				top: 0; }
			.hit_block_article_img_caption:after {
				content: url('../img/star.svg'); }
			.hit_block_article_img_caption {
				background: #fcd43e;
				display: inline-block;
				padding: 7px 5px;
				position: absolute;
				top: 0;
				right: 0;
				z-index: 2;
				* {
					display: inline-block;
					margin: 0; } } }


		.hit_block_article_name {
			color: $main-blue;
			margin-top: 5px;
			line-height: 20px; }
		.hit_block_button {
			background: $main-blue url('../img/hit_block_button.png') no-repeat 0 0;
			background-size: cover;
			@include box-shadow(0 6px 10px rgba(0, 0, 0, 0.2));
			border: none;
			color: #fff;
			padding: 10px 30px;
			margin-top: 12px; } } }



.we_are {
	@include flex;
	justify-content: space-between;
	margin-top: 30px;
	background: #fff;
	padding: 30px 15px 25px;
	.we_are_block {
		width: 33%;
		text-align: center;
		.we_are_block_h {
			margin-top: 15px;
			margin-bottom: 5px;
			h5 {
				font-weight: bold; } }
		.we_are_block_text {
			font-size: 14px; } } }

.popular_product {
	// margin-bottom: 100px
	margin-top: 33px;
	@include flex;
	justify-content: space-between;
	flex-wrap: wrap;
	.popular_product_h {
		width: 100%;
		margin-bottom: 22px;
		font-size: 30px;
		h2 {
			font-weight: 700; } }
	.popular_product_cart {
		width: 23.4%;
		background: #fff;
		padding: 20px;
		padding-bottom: 25px;
		border-bottom: 1px solid #f3f3f3;
		margin-bottom: 20px;
		.popular_product_cart_img {
			height: 146px;
			position: relative;
			// text-align: center
			img {
				vertical-align: middle;
				position: absolute;
				// position: relative
				top: 50%;
				left: 50%;
				@include transform(translate(-50%, -50%));
				display: block;
				width: 100%; } }
		.popular_product_cart_capt {
			margin-top: 20px;
			font-size: 16px;
			color: $main-blue;
			line-height: 20px; }
		.popular_product_cart_text {
			margin-top: 10px;
			color: #666;
			font-size: 14px; }
		.popular_product_cart_price {
			margin-top: 10px;
			font-size: 16px;
			color: #000; }
		.popular_product_cart_button {
			margin-top: 15px;
			@include box-shadow(0 6px 10px rgba(0, 0, 0, 0.2));
			background: #ffee58 url('../img/product_card_btn.png') no-repeat 0 0;
			background-size: cover;
			border: none;
			padding: 10px 30px;
			width: 100%;
			color: #8d6207;
			cursor: pointer; } } }


.baner {
	// margin-bottom: 100px
	@include flex;
	justify-content: space-between;
	align-items: center;
	background: $main-blue url('../img/baner-bg.png') no-repeat 0 0;
	background-size: cover;
	padding: 15px 20px 12px;
	.baner_h {
		margin-bottom: 0;
		font-size: 22px;
		line-height: 22px; }
	.baner_text {
		color: #fff;
		font-size: 16px;
		p {
			margin: 0; }
		p:last-child {
			font-size: 18px; } }
	.baner_social {
		.baner_social_icon:last-child {
			margin-right: 0; }
		.baner_social_icon {
			background: #ffee58 url('../img/product_card_btn.png') no-repeat 0 0;
			display: inline-block;
			width: 40px;
			height: 40px;
			position: relative;
			text-align: center;
			margin-right: 7px;
			img {
				// position: absolute
				vertical-align: top;
				position: relative;
				top: 50%;
				// left: 50%
				// @include transform(translate(-50%, -50%))
				@include transform(translate(0%, -50%)); } } }
	.baner_button {
		padding: 0px 26px;
		height: 40px;
		color: #8d6207;
		font-size: 16px;
		background: #ffee58 url('../img/product_card_btn.png') no-repeat 0 0;
		background-size: cover;
		border: none;
		cursor: pointer; } }


.about_shop {
	margin-top: 30px;
	.about_shop_h {
		font-size: 30px;
		h3 {
			font-weight: 700; } }
	.about_shop_block:nth-child(2) {
		.about_shop_block_img {
			margin-left: 30px; } }
	.about_shop_block:nth-child(3) {
		.about_shop_block_img {
			margin-right: 30px; } }
	.about_shop_block {
		@include flex;
		justify-content: space-between;
		// div
		/// display: inline-block
		.about_shop_block_text {
			color: #222;
			font-size: 14px;
			line-height: 16px;
			p {
				margin-bottom: 10px; } }
		// .about_shop_block_mobile_hide
		// 	display: block
		.about_shop_mobile_btn {
			display: none; }
		.about_shop_block_img {
			position: relative;
			img {
				position: relative;
				top: 50%;
				left: 50%;
				@include transform(translate(-50%, -50%)); } } } }

.hide {
	display: none !important;
 }	// display: block

footer {
	background: #2f2f2f; }
.footer_menu {
	@include flex;
	justify-content: space-between;
	color: #666;
	padding: 35px 15px;
	a:hover {
		text-decoration: none;
		color: lighten(#666666, 20%); }
	a {
		color: #666;
		// text-decoration: underline
		border-bottom: 1px solid #666666;

		@include transition-ease(0.3s); }
	.footer_block_h {
		* {
			font-weight: bold; } }
	.footer_block_list {
		list-style-type: none;
		padding: 0;
		.footer_block_list_item {
			margin-bottom: 5px;
			line-height: 22px; } }
	.footer_block_callback {
		// text-decoration-style: dotted
		// line-height: 20px
		border-bottom: 1px dotted #666666; }
	.footer_block_soc {
		margin-top: 20px;
		.footer_block_soc_icon:hover {
			background: lighten(#666666, 20%); }
		.footer_block_soc_icon {
			display: inline-block;
			position: relative;
			width: 30px;
			height: 30px;
			background: #666666;
			border-radius: 50%;
			border: none;
			img {
				position: absolute;
				top: 50%;
				left: 50%;
				@include transform(translate(-50%, -50%)); } } }
	.footer_logo {
		margin-bottom: 20px;
		border: none;
		display: inline-block; }
	.footer_block_text {
		font-size: 14px;
		line-height: 18px; }
	.footer_block_4 {
		max-width: 410px; } }

.footer_company_block {
	background: #262626;
	color: #474747;
	text-align: center;
	font-size: 14px;
	padding: 18px 20px;
	a {
		color: #474747; }
	p {
		margin: 0; } }
